*/

body {
  background-color: #b8c6db;
  background-image: linear-gradient(
    315deg,
    #b8c6db
      0%,
    #f5f7fa
      100%
  );
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Poppins",
    sans-serif;
  margin: 0;
  min-height: 100vh;
}

.quiz-container {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0
    0
    10px
    2px
    rgba(
      100,
      100,
      100,
      0.1
    );
  overflow: hidden;
  width: 600px;
  max-width: 100%;
}

.quiz-header {
  padding: 4rem;
}

h2 {
  padding: 1rem;
  text-align: center;
  margin: 0;
}

ul {
  list-style-type: none;
  padding: 0;
}

ul
  li {
  font-size: 1.2rem;
  margin: 1rem
    0;
}

ul
  li
  label {
  cursor: pointer;
}

button {
  background-color: #8e44ad;
  border: none;
  color: white;
  cursor: pointer;
  display: block;
  font-family: inherit;
  font-size: 1.1rem;
  width: 100%;
  padding: 1.3rem;
}

button:hover {
  background-color: #732d91;
}

button:focus {
  background-color: #5e3370;
  outline: none;
}
